:root {
  /* Base colors */
  --yellow-primary: #FFD700;
  --yellow-secondary: #F4C430;
  --yellow-light: #FFF8DC;
  --yellow-dark: #B8860B;
  
  /* Amber colors */
  --amber-50: #fffbeb;
  --amber-100: #fef3c7;
  --amber-200: #fde68a;
  --amber-300: #fcd34d;
  --amber-400: #fbbf24;
  --amber-500: #f59e0b;
  --amber-600: #d97706;
  --amber-700: #b45309;
  --amber-800: #92400e;
  --amber-900: #78350f;
  --amber-950: #451a03;
  
  /* Theme defaults */
  --text-primary: var(--amber-400);
  --text-secondary: var(--amber-200);
  --bg-primary: #000000;
  
  /* UI Colors */
  --background: 0 0% 3.9%;
  --foreground: 0 0% 98%;
  --card: 0 0% 3.9%;
  --card-foreground: 0 0% 98%;
  --popover: 0 0% 3.9%;
  --popover-foreground: 0 0% 98%;
  --primary: var(--yellow-primary);
  --primary-foreground: 0 0% 9%;
  --secondary: 0 0% 14.9%;
  --secondary-foreground: 0 0% 98%;
  --muted: 0 0% 14.9%;
  --muted-foreground: 0 0% 63.9%;
  --accent: 0 0% 14.9%;
  --accent-foreground: 0 0% 98%;
  --destructive: 0 62.8% 30.6%;
  --destructive-foreground: 0 0% 98%;
  --border: 0 0% 14.9%;
  --input: 0 0% 14.9%;
  --ring: 0 0% 83.1%;
  --radius: 0.5rem;
}

[data-theme="light"] {
  --text-primary: var(--amber-700);
  --text-secondary: var(--amber-800);
  --bg-primary: #FFFAF0;
  --background: 0 0% 100%;
  --foreground: 0 0% 3.9%;
  --card: 0 0% 100%;
  --card-foreground: 0 0% 3.9%;
  --popover: 0 0% 100%;
  --popover-foreground: 0 0% 3.9%;
  --primary: var(--yellow-dark);
  --border: 0 0% 89.8%;
  --input: 0 0% 89.8%;
}

[data-theme="dark"] {
  --text-primary: var(--amber-400);
  --text-secondary: var(--amber-200);
  --bg-primary: #000000;
  --background: 0 0% 3.9%;
  --foreground: 0 0% 98%;
  --card: 0 0% 3.9%;
  --card-foreground: 0 0% 98%;
  --popover: 0 0% 3.9%;
  --popover-foreground: 0 0% 98%;
  --primary: var(--yellow-primary);
  --border: 0 0% 14.9%;
  --input: 0 0% 14.9%;
}

